import { useNavigate } from "react-router-dom";
import { useEffect } from "react";

//  URL    
// useNavigate setTimeout    
export default function NotFound() {
  const navigate = useNavigate();

  //     , useEffect 
  useEffect(() => {
    setTimeout(() => {
      // -> App.tsx (-> Coins.tsx)
      navigate("/");
    }, 2000);
  }, [navigate]);

  return <h1>404 Not Found</h1>;
}
